import axios from 'axios'
const url = "https://bookshop-react-js-express-js.onrender.com/api"

export async function getBooks({ seed , language , likes , reviews , booksCount }) {
   try {
     const res = await axios.get(`${url}/` , {
        params: {
            language: language,
            seed: seed,
            likes: likes,
            reviews: reviews,
            booksCount: booksCount,
        }
    })
    return res
   } catch (error) {
    console.log(error)
    return error 
   }
}