import "./Gallery.css";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBCard,
  MDBCardText,
  MDBCardBody,
  MDBCardImage,
} from "mdb-react-ui-kit";

export default function Gallery({ books }) {
  return (
    <MDBContainer
      className="py-5"
      style={{ display: "flex", gap: "10px", flexWrap: "wrap" }}
    >
      {books.map((book, i) => {
        return (
          <MDBCol style={{ minWidth: "225px" }} className="hover" key={i}>
            <MDBCard style={{ position: "relative" }}>
              <MDBCardImage
                src={book.photo}
                alt="..."
                position="top"
                style={{ aspectRatio: "1:1" }}
              />
              <p className="bookview" style={{ top: "40px" }}>
                {book.title}
              </p>
              <p className="bookview" style={{ top: "300px" }}>
                {book.authors}
              </p>
              <p
                className="bookview"
                style={{ top: "320px", fontSize: "12px" }}
              >
                {book.ISBN}
              </p>
              <MDBCardBody>
                <MDBCardText>
                  <MDBRow>{book.title}</MDBRow>
                  <MDBRow>{book.authors}</MDBRow>
                  <MDBRow>{book.publisher}</MDBRow>
                  <MDBRow>{book.year}</MDBRow>
                </MDBCardText>
              </MDBCardBody>
            </MDBCard>
          </MDBCol>
        );
      })}
    </MDBContainer>
  );
}
