import "./Table.css";
import {
  MDBRow,
  MDBCol,
  MDBAccordion,
  MDBAccordionItem,
  MDBBadge,
  MDBIcon,
} from "mdb-react-ui-kit";

export default function Table({ books }) {
  return (
    <MDBCol>
      <MDBRow className="TableHeader" style={{ fontWeight: "bold" }}>
        <MDBRow style={{ padding: "0 24px" }}>
          <MDBCol md={1}>#</MDBCol> <MDBCol>ISBN</MDBCol> <MDBCol>Title</MDBCol>
          <MDBCol>Author(s)</MDBCol> <MDBCol>Publicher</MDBCol>
        </MDBRow>
        <MDBCol md={20} style={{ width: "20px" }}>
          {" "}
        </MDBCol>
        <MDBCol md={100}>
          <hr style={{ borderTop: "solid 2px #000" }} />
        </MDBCol>
      </MDBRow>
      <MDBRow>
        <MDBAccordion className="accordionBooks">
          {books.map((book, i) => {
            return (
              <MDBAccordion>
                <MDBAccordionItem
                  bodyStyle={{ display: "flex", flexWrap: "wrap", gap: "10px" }}
                  collapseId={1}
                  headerTitle={
                    <MDBCol style={{ fontWeight: "bold" }}>
                      <MDBRow key={i}>
                        <MDBCol md={1}>{book.id}</MDBCol>{" "}
                        <MDBCol>{book.ISBN}</MDBCol>
                        <MDBCol>{book.title}</MDBCol>{" "}
                        <MDBCol>{book.authors}</MDBCol>
                        <MDBCol>{book.publisher}</MDBCol>
                      </MDBRow>
                    </MDBCol>
                  }
                >
                  <MDBCol
                    md={1}
                    style={{ width: "240px", position: "relative" , textAlign: "center"}}
                  >
                    <img
                      src={book.photo}
                      className="img"
                      style={{
                        width: "240px",
                        height: "320px",
                        padding: "10px 20px",
                      }}
                    />
                    <p className="bookview" style={{top: "40px"}}>{book.title}</p>
                    <p className="bookview" style={{bottom: "40px"}}>{book.authors}</p>
                    <p className="bookview" style={{bottom: "20px" , fontSize: "12px"}}>{book.ISBN}</p>
                    <div className="mx-3" href="" style={{color: "#557ffdff"}}> 
                      <MDBIcon fas icon="thumbs-up" size="lg" />
                      <MDBBadge
                        style={{ transform: "translate(2.5px, -5px)" }}
                        color="danger"
                        notification
                        pill
                      >
                        {book.likes}
                      </MDBBadge>
                    </div>
                  </MDBCol>
                  <MDBCol style={{ fontWeight: "bold" }}>
                    <MDBRow style={{ color: "black" }}>
                      <h3 style={{ fontWeight: "bold" }}>{book.title}</h3>
                    </MDBRow>
                    <MDBRow style={{ color: "black" }}>
                      <h4 style={{ fontWeight: "bold" }}>
                        by<i> {book.authors}</i>
                      </h4>
                    </MDBRow>
                    <MDBRow>
                      <h5>
                        {book.publisher},{book.year}
                      </h5>
                    </MDBRow>
                    <MDBRow style={{ color: "black" }}>
                      <h5 style={{ fontWeight: "bold" }}>Review</h5>
                    </MDBRow>
                    {Array.isArray(book.reviews) && book.reviews.length > 0 ? (
                      book.reviews.map((review, i) => {
                        return (
                          <MDBRow>
                            <MDBRow>
                              <h5>{review.review}</h5>
                            </MDBRow>
                            <MDBRow>
                              <h6>-{review.author}</h6>
                            </MDBRow>
                          </MDBRow>
                        );
                      })
                    ) : (
                      <MDBRow>
                        <h6>no review</h6>
                      </MDBRow>
                    )}
                  </MDBCol>
                </MDBAccordionItem>
              </MDBAccordion>
            );
          })}
        </MDBAccordion>
      </MDBRow>
    </MDBCol>
  );
}
