import "./Table.css";
import { useEffect , useRef } from "react";
import { InView, useInView } from "react-intersection-observer";
import * as xlsx from "xlsx";
import { saveAs } from "file-saver";
import {
  MDBInput,
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBRange,
  MDBBtn,
} from "mdb-react-ui-kit";
import "mdb-react-ui-kit/dist/css/mdb.min.css";
import { RandomSvg, GallerySvg } from "./svgs.jsx";
import { useState } from "react";
import Table from "./Table.jsx";
import { getBooks } from "../queries.js";
import Gallery from "./Gallery.jsx";
import { useDebounce } from "use-debounce";

export default function Toolbar() {
  const [language, setLanguage] = useState(0);
  const [seed, setSeed] = useState(1);
  const [likes, setLikes] = useState(0);
  const [reviews, setReviews] = useState(0);
  const [booksCount, setBooksCount] = useState(20);
  const [books, setbooks] = useState([]);
  const [layout, setLayout] = useState(true);
  const [debouncedLikes] = useDebounce(likes, 500);
  const [debouncedSeed] = useDebounce(seed, 500);
  const [debouncedLanguage] = useDebounce(language, 500);
  const [debouncedReviews] = useDebounce(reviews, 500);
  const [debouncedBooksCount] = useDebounce(booksCount, 500);
  const isFetching = useRef(false);

  const { ref, inView } = useInView({
    threshold: 0.5,
    delay: 300,
    onChange: (inView) => {   
      if (inView && !isFetching.current) {
      isFetching.current = true;
      setBooksCount(prev => prev + 20);
    }}
  })
  useEffect(() => {
    isFetching.current = false;
  }, [books]);
  async function handleBooks() {
    try {
      const res = await getBooks({
        seed: debouncedSeed,
        language:debouncedLanguage,
        likes:debouncedLikes,
        reviews:debouncedReviews,
        booksCount: debouncedBooksCount,
      });
      setbooks([...res.data]);
    } catch (error) {
      console.log(error);
    }
  }
  useEffect(() => {
    handleBooks()
  }, [debouncedLanguage,debouncedSeed,debouncedLikes,debouncedReviews,inView]);

  function saveTable() {
    const sheet = xlsx.utils.json_to_sheet(books);
    const file = xlsx.utils.book_new();
    xlsx.utils.book_append_sheet(file, sheet, "Books table");
    const saveBook = xlsx.write(file, {
      bookType: "xlsx",
      type: "array",
    });
    const blob = new Blob([saveBook], {
      type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
    });
    saveAs(blob, "table");
  }

  return (
    <MDBContainer className="py-5">
      <MDBRow className="toolbar">
        <MDBCol className="d-flex" style={{ gap: "5px" }}>
          <MDBCol>
            <MDBInput
              label="Seed"
              id={"seedInput"}
              value={seed}
              onChange={(e) => setSeed(e.target.value)}
              type="number"
              max="4294967295"
            />
          </MDBCol>
          <MDBCol md={2.5}>
            <MDBBtn
              onClick={() => {
                const rndm = Math.floor(Math.random() * 4294967296);
                setSeed(rndm);
              }}
            >
              <RandomSvg></RandomSvg>
            </MDBBtn>
          </MDBCol>
        </MDBCol>
        <MDBCol>
          <div className="form-outline mb-4">
            <select
              value={language}
              onChange={(e) => setLanguage(e.target.value)}
              className="form-select"
              id="select"
            >
              <option value="0" defaultChecked={true}>
                English
              </option>
              <option value="1">Russian</option>
              <option value="2">French</option>
            </select>
          </div>
        </MDBCol>
        <MDBCol>
          <label
            htmlFor="customRange3"
            className="form-label"
            style={{ margin: "0" }}
          >
            Likes
          </label>
          <MDBRange
            min={0}
            max={5}
            step="0.1"
            value={likes}
            onChange={(e) => setLikes(e.target.value)}
          />
        </MDBCol>
        <MDBCol>
          <MDBInput
            className="active"
            max={10}
            maxLength={10}
            type="number"
            step="0.1"
            label="Reviews"
            value={reviews}
            onChange={(e) =>
              e.target.value <= 10 && e.target.value >= 0
                ? setReviews(e.target.value)
                : setReviews(10)
            }
          />
        </MDBCol>
        <MDBCol>
          <MDBBtn onClick={() => setLayout(true)}></MDBBtn>{" "}
          <MDBBtn onClick={() => setLayout(false)}>
            <GallerySvg></GallerySvg>
          </MDBBtn>
        </MDBCol>
        <MDBCol>
          <MDBBtn onClick={saveTable}>Export to CSV</MDBBtn>
        </MDBCol>
      </MDBRow>
      {layout ? (
        <Table books={books} booksCount={booksCount}></Table>
      ) : (
        <Gallery books={books}></Gallery>
      )}
      {<div className="view" ref={ref}>...</div>}
    </MDBContainer>
  );
}
